.base-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0px 0px 12px 2px rgba(15, 15, 15, 0.2);
    border-radius: 4px;
    width: 50%;
    padding: 17px 10px;
    margin-top: 20px;
    background-image: linear-gradient(-60deg, #6c3 50%, #09f 50%);
}

.header {
    font-size: 24px;
    font-family: "Open Sans", sans-serif;
}
.content {
    display: flex;
    flex-direction: column;
}
.image {
    width: 21em;
}
.img {
    width: 100%;
    height: 100%;
}
  
.form {
    margin-top: 2em;
    display: flex;
    flex-direction: column;
    align-items: center;
}        
.form-group {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: fit-content;
}
label {
    font-size: 20px;
}
input {
    margin-top: 6px;
    min-width: 18em;
    height: 37px;
    padding: 0px 10px;
    font-size: 16px;
    font-family: "Open Sans", sans-serif;
    background-color: #f3f3f3;
    border: 0;
    border-radius: 4px;
    margin-bottom: 31px;
    transition: all 250ms ease-in-out;
}
input:hover {
    background-color: #ffffff;
    box-shadow: 0px 0px 14px 0.3px #0e81ce96;
}
input:focus {
    outline: none;
    box-shadow: 0px 0px 12px 0.8px #3474dbb2;
}
  
.footer {
      margin-top: 3em;
}

.login {
    width: 27em;
    height: 40em;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 5px;
    position: relative;
    z-index: 99;
 }
 